.register-container {
  width: 400px;
  margin: 0 auto;
  padding: 20px;
  background:rgb(200, 189, 189);
  border-radius: 12px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.1);
}

.form-title {
  text-align: center;
  margin-bottom: 20px;
}

.form input,
.form select {
  width: 100%;
  padding: 10px;
  margin-bottom: 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.password-group {
  display: flex;
  gap: 10px;
  align-items: center;
}

.generate-btn {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.generate-btn:hover {
  background-color: #0056b3;
}

.password-hint {
  font-size: 12px;
  color: #666;
  margin-bottom: 10px;
}

.submit-btn {
  width: 100%;
  padding: 12px;
  background-color: #28a745;
  border: none;
  color: white;
  font-weight: bold;
  border-radius: 6px;
  cursor: pointer;
}

.submit-btn:hover {
  background-color: #218838;
}
